header {
    display: flex;
    flex-direction: row;

    padding: 1rem;
    height: 100px;
    align-items: center;
    text-align: center;
    gap: 20px;
    
}

.sidebar-img {
    transform: rotate(0deg);

    object-fit: cover;
    width: 50px;
    height: 50px;
    filter: invert(40%);
    transition: 1s ease;
}

.sidebar-img:hover {
    transform: rotate(225deg);
    filter: invert(80%);
}

.nav-bar {
    display: flex;
    flex-direction: row;
    gap: 30px;
    flex:1;
}

.nav-bar li {
    list-style: none;
    font-size: 25px;
    color: rgb(188, 188, 188);
    transition: 0.3s ease;
    &:hover {
        color: rgb(224, 204, 255);
        /* transform: scale(1.1); */
        padding: 0 20px;

    }
}

.home-title {
    flex:1;

}

.page {
    flex:1;

}

.dropdown-dev {
    opacity: 0;
    z-index: 3;
    transition: 0.3s ease;
    pointer-events: none;

    position: absolute;
    top: 11%;
    left:7.5%;
    
    transform: translateY(10px);

    /* background-color: white;
    color: black; */
    padding: 10px;
    border-radius: 0.5rem;

    & li {
        list-style: none;
        padding: 0.2rem;
    }
}

.dropdown-dev.open {
    opacity: 1;
    transform: translateY(0);
    pointer-events: all;
}